import React, {Component} from 'react';
import {
	Box, 
	Button,
	Heading,
	Image,
	Paragraph,
	Meter,
	Stack,
	Text
} from 'grommet';

import {
	Add
} from 'grommet-icons';

class ClassView extends Component {

	state = {
		classInfo: this.props.defaultInfo
	}

	componentWillReceiveProps(props) {
		this.setState({ classInfo: props.classInfo });
	}
	
	render ( ) {
		const classInfo = this.state.classInfo;
		return (
			<Box
				flex
				direction='column'
				justify='start'
				align='start'
				pad='5%'
				animation={{
					type: 'fadeIn',
					delay: 50,
					duration: 750,
					size: 'large'
                }}
                style={{
                    overflow: 'auto'
                }}
			>
				<Heading level='1'>{classInfo.degreeName+' '+classInfo.classNumber}</Heading>
				<Heading level='2'>{classInfo.className}</Heading>
				<Text size='large'>{classInfo.units} units</Text>
				<Paragraph>
					{classInfo.summary}
				</Paragraph>
				{classInfo.instructorName && 
				<Heading level='1'>Instructors</Heading>
				}
				<Heading level='2'>{classInfo.instructorName || 'No instructors found'}</Heading>
				{classInfo.rmpID !== 0 &&  
					<Box 
						direction='row'
						align='center'
						justify='between'
						gap='medium'
						margin={{top: 'medium'}}
					>
						<Box align="start">
							<Stack anchor="center">
								<Meter
								type="circle"
								background="light-2"
								values={[{
									value: 70,
									color: 'main',
									highlight: true,
								}]}
								size="xsmall"
								thickness="small"
								/>
								<Box direction="row" align="center" pad={{ bottom: "xsmall" }}>
								<Text size="xlarge" weight="bold">
									3.5
								</Text>
								<Text size="small">/5</Text>
								</Box>
							</Stack>
						</Box>
						<Image style={{height: '100px'}} src='/SycamoreScheduler/rmp.png' />
					</Box>
				}
				{this.props.isSignedIn && 
					<Button 
						hoverIndicator
						icon={<Add></Add>} 
						label="add to course plan"
						background='main' 
						style={{
							fontFamily: 'Inconsolata'
						}}
						pad='small'
						margin={{
							top: 'large'
						}}
					>
					</Button>
				}
			</Box>
		);
	}
}

export default ClassView;